.infoCard {
  display: flex;
  flex-direction: column;
  border: solid 1px rgba(128, 128, 128, 0.49);
  border-radius: 16px;
  background-color: white;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  margin: 1rem;
  height: 55rem;
}

.infoCard > div {
  margin: 1rem;
}
.infoCard > div > h1,.infoCard > div > p {
    margin:  1rem 0;
}

.infoCard > div > .text,
.infoCard > div > .description,
.infoCard > div > .image {
  margin: 1rem 0;
}

.infoCard input,
.infoCard textarea {
  margin: 5px 0;
  width: 23rem;
  height: 2rem;
  border-radius: 8px;
  border: solid 1px #7a7a7a;
  padding: 0 1rem;
  font-weight: unset;
  font-family: 'Poppins', sans-serif;
}
.infoCard textarea {
  padding: 1rem;
}

.infoCard input:focus,
.infoCard textarea:focus {
  border: none;
  outline-color: #4e42b8;
}
